.navbar-nav{
    display: flex;
    flex-direction: column;
    padding-left: 5vw ;

    margin-bottom: 0;
    margin-left: auto;
    list-style: none;
}
.navbar {
  background-color: azure !important;
}
.navbar-brand {
  font-size: 1.9rem;
  color: antiquewhite;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;

}

.menu_active {
  
    
  padding: 1.5rem 1rem;
  font-weight: bold;
  border-bottom: 1px solid plum;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  
}
.navbar a:hover{
  color: aqua;
}
@media (max-width: 991px){
  .navbar-brand{
    font-size: 1.5rem;
    
  }
  .menu_active{
    word-spacing: 2rem;
    font-weight: bold;
    
  }
}
#header {
  width: 100%;
  height: 80vh;
  margin-top: 50px;
}
#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: aquamarine;
}
#header h2{
  color: blueviolet;
  font-size: 24px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: aqua;
  text-decoration: none;
  border: 1px solid blue;
}
#header .btn-get-started:hover{
  background: #3498db;
  color: antiquewhite;
}
.header-img{
  text-align: right;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(248, 246, 253);
}

body,
input,
textarea,
button {
  font-family: "Inter", sans-serif;
}

.contact {
  width: 200px;
  margin: 0 auto;
  height: 100vh;
}

/* Contact.js */
.form {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form > h1 {
  margin-bottom: 30px;
}

.form > input,
textarea {
  padding: 20px;
  border-radius: 3px;
  /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
  margin-bottom: 20px;
  border: 1px solid lightgray;
  /* border: none; */
  background: #fff;
  font-size: 16px;
  color: rgb(0, 0, 32);
  outline: none;
}

.form > input:focus,
textarea:focus {
  border: 1px solid rgb(0, 0, 196);
}

.form > textarea {
  height: 150px;
  max-width: 400px;
  min-height: 100px;
}

.form > label {
  padding-bottom: 10px;
  color: rgb(0, 0, 32);
  font-weight: bold;
}

.form > button {
  padding: 20px;
  border: none;
  background-color: rgb(2, 2, 110);
  font-weight: 500;
  font-size: 20px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  margin-top: 10px;
}

.form > button:hover {
  background-color: rgb(0, 0, 196);
}
